import Cookies from 'js-cookie'
import axios from 'axios'
export default {
  name: 'list',
  data () {
    return {
      okProject: false,
      list: [],
      pname: '',
      pid: Number,
      tag: this.TaskWaitForCheck
    }
  },
  methods: {
    getProjectList () {
      axios.get(this.url + '/project/manager/' + Cookies.get('id'))
        .then(this.getProjectListSucc)
    },
    getProjectListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.list = res
      }
    },
    handleOKProject (pid, pname) {
      this.okProject = true
      this.pname = pname
      this.pid = pid
    },
    handleEditProject (pid, pname) {
      sessionStorage.setItem('pname', pname)
      sessionStorage.setItem('pid', pid)
      sessionStorage.setItem('activeMenu', '1')
      this.$router.push('/checkProjectDetail')
    },
    OKProject () {
      this.$axios({
        method: 'put',
        url: this.url + '/project/',
        data: {
          id: this.pid,
          state: this.Run
        }
      }).then((res) => {
        if (res.status === 200) {
          alert('审核成功！')
          axios.get(this.url + '/project/manager/' + Cookies.get('id'))
            .then(this.getProjectListSucc)
          this.okProject = false
        }
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.getProjectList()
  }
}



